export default{
	props:{
		buymoney:[String,Number],
		discount:[String,Number],
		inpValue:String
		},
	computed:{
		// 实现双向绑定
		newValue:{
			get(){
				return this.inpValue
			},
			set(value){
				this.$emit('input',value)
			}
		}
	}
	
}

